import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/views/login'
import Layout from '@/layout/layout'

Vue.use(Router)

/**
 * alwaysShow: true           if set true, will always show the root menu, whatever its child routes length
 *                            if not set alwaysShow, only more than ont route under the children
 *                            it will becomes nested mode, otherwise not show the root menu
 * alwaysShow: true           如果设置为true,它将总是现在在根目录。如果不设置的话，当它只有1个子路由的时候，会把
 *                            它的唯一子路由放到跟目录上来，而不显示它自己本身。
 */

export const constantRouterMap = [
  {
    path: '/login',
    name: 'login',
    hidden: true,
    component: Login,
    meta: {
      title: '登录'
    }
  },
  {
    path: '/404',
    component: () => import('@/views/error-page/404'),
    hidden: true
  },
  {
    path: '/',
    hidden: true,
    component: Layout,
    redirect: '/home',
    children: [
      {
        path: 'home',
        name: 'home',
        component: () => import('@/views/homepage'),
        meta: { title: '首页' }
      }
    ]
  },
  {
    path: '/user',
    component: Layout,
    hidden: true,
    meta: {
      icon: 'tickets',
      title: '个人中心'
    },
    children: [
      {
        path: 'profile',
        name: 'Profile',
        component: () => import('@/views/user/profile'),
        meta: { icon: 'warning', title: '个人中心' }
      },
      {
        path: 'avatar',
        name: 'Avatar',
        component: () => import('@/views/user/profile'),
        meta: { icon: 'warning', title: '修改头像' }
      }
    ]
  },
  {
    path: '/gm',
    component: Layout,
    redirect: '/gm/banuser',
    alwaysShow: true,
    meta: {
      icon: 'svg-aperture',
      title: 'GM指令'
    },
    children: [
      {
        path: 'banuser',
        name: 'banuser',
        component: () => import('@/views/gm/banuser'),
        meta: { icon: 'svg-aperture', title: '封号' }
      },
      {
        path: 'blacklist',
        name: 'blacklist',
        component: () => import('@/views/gm/blacklist'),
        meta: { icon: 'svg-aperture', title: '封号名单' }
      }
    ]
  },
  {
    path: '/mail',
    component: Layout,
    redirect: '/mail/system_mail',
    alwaysShow: true,
    meta: {
      icon: 'svg-aperture',
      title: '邮件'
    },
    children: [
      {
        path: 'system_mail',
        name: 'system_mail',
        component: () => import('@/views/mail/system_mail'),
        meta: { icon: 'svg-aperture', title: '系统邮件' }
      },
      {
        path: 'user_mail',
        name: 'user_mail',
        component: () => import('@/views/mail/user_mail'),
        meta: { icon: 'svg-aperture', title: '个人邮件' }
      }
    ]
  },
  {
    path: '/SVN',
    component: Layout,
    redirect: '/SVN/svn_lock',
    // alwaysShow: true,
    meta: {
      icon: 'svg-aperture',
      title: 'SVN管理'
    },
    children: [
      {
        path: 'svn_lock',
        name: 'svn_lock',
        component: () => import('@/views/SVN/svn_lock'),
        meta: { icon: 'svg-aperture', title: '版本锁定管理' }
      }
    ]
  },
  {
    path: '/Statics',
    component: Layout,
    redirect: '/Statics/pass_ratia',
    // alwaysShow: true,
    meta: {
      icon: 'svg-aperture',
      title: '通关率'
    },
    children: [
      {
        path: 'pass_ratia',
        name: 'pass_ratia',
        component: () => import('@/views/Statics/pass_ratia'),
        meta: { icon: 'svg-aperture', title: '通关率' }
      }
    ]
  }
]

export default new Router({
  // mode: 'history',  require service support
  // scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

export const asyncRouterMap = [

]


